@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "inter";
}

h3 {
  text-transform: capitalize;
}

body {
  background-color: white;
  height: 100vh;
  width: 100%;
  font-family: "Inter", sans-serif;
}

.App {
  height: 100vh;
  width: 100%;
  background-color: white;
  color: black;
  transition: 0.2s ease-in-out;
  nav {
    height: 70px;
    background-color: #d9d9d9;
    padding: 10px;
    ul {
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 100%;
      img {
        height: 30px;
        cursor: pointer;
      }
      .themeChange {
        height: 100%;
        display: flex;
        align-items: center;
        img {
          height: 30px;
        }
      }
    }
  }
  .main {
    // border: 1px solid violet;
    margin: 20px;
    .search {
      display: grid;
      // gap: 10px;
      width: 100%;
      // border: 1px dashed;
      font-weight: 500;
      font-size: 1.2rem;
      padding-block: 50px;
      .inputAndArrow {
        display: flex;
        flex-direction: row;
        align-items: center;
        height: 50px;
        width: 100%;
        // border: 1px solid red;
        gap: 10px;
        input {
          border: 3px solid black;
          outline: none;
          width: 100%;
          padding: 10px;
          font-weight: 600;
          font-family: "Inter";
          font-size: 1.2rem;
        }
        img {
          height: 100%;
          background-color: #d9d9d9;
          padding: 10px;
          width: 100px;
          cursor: pointer;
        }
      }
    }
    .cards {
      display: grid;
      gap: 20px;
      .initial {
        display: flex;
        gap: 15px;
        font-weight: 600;
        font-size: x-large;
        text-transform: capitalize;
      }
      .card{
        display: flex;
        flex-direction: column;
        gap: 10px;
        p{
          border-left: 10px solid rgb(99, 210, 253);
          padding-inline: 5px;
        }
      }
    }
  }
  footer{
    width: 100%;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    bottom: 0;
    // border: 1px solid white;
    gap: 5px;
    padding: 20px;
    a{
      color: black;
    }
  }
}

.App.darkApp{
  background-color: rgb(23, 0, 49);
  color: white;
  footer{
    a{
      color: white;
    }
  }
}


@media screen and (min-width:500px) {
  .App{
    nav{
      padding-inline: 100px;
    }
    .main{
      padding-inline: 100px;
    }
  }
}

@media screen and (min-width:701px) {
  .App{
    nav{
      padding-inline: 100px;
    }
    .main{
      padding-inline: 200px;
    }
  }
}


@media screen and (min-width:1000px) {
  .App{
    nav{
      padding-inline: 200px;
    }
    .main{
      padding-inline: 400px;
    }
  }
}